import React, { useState } from "react";
import Layout from "../components/layout"
import SEO from "../components/seo"
import "./projects.css";
import { Link } from "gatsby";
import Modal from "../components/modal";
import {civil_data, mining_data} from "../data/project_data";


const Text_box = (props) => {
  const data = props.data;
  const [clicked_index, setClicked_index] = useState();

  const sendIndex = (index) => {
    let max_number = data[index].image_array.length - 1;
    // console.log("pro max " + max_number);

    var modal = <Modal 
    clicked_index={0}
    data_sources={data[index].image_array}
    close_modal={() => setClicked_index(null)}
    max_number = {max_number}
    />;
    setClicked_index(modal);
  }

  return(
    <ul>
      {data.map((e,i) => 
      <li key={i}
        onClick={() => sendIndex(i)}>
        <div className="text_box">
          <span>
            <i className="fas fa-users blue "></i><br/>
            <i className="far fa-clipboard blue"></i>
          </span>
          <span>
            <h3>{e.client}</h3>
            <h3>{e.title}</h3>
          </span>
          <span>
            <img 
              src={e.image_array[0].image} 
              alt="project sample" 
              width="80" 
              height ="80"
            />
          </span>
        </div>
      </li>
      )}
      {clicked_index}
    </ul>
  )
}


export default function Project(props) {
  return (
    <>
      <Layout>
      <SEO title="Home" />

      <div id="project_container">
        <section className="category_list">
          <ul>
            <li><Link to="#civil">civil/ architecture</Link></li>
            <li><Link to="#minig">minig/ heavy industory</Link></li>
            <li><Link to="/">automotive</Link></li>
            <li><Link to="/">water</Link></li>
            <li><Link to="/">oil/ gas</Link></li>
            <li><Link to="/">power</Link></li>
            <li><Link to="/">robotics/ automation</Link></li>
          </ul>
        </section>
        
        <section className="project_list">
          <div id="list_wrapper">
            <h1>Projects</h1>
              <div className="list_box">
                <h2 id="civil">civil/architecture</h2>
                <Text_box data={civil_data} />
              </div>
              <div className="border"></div>
              <div className="list_box">
                <h2 id="minig">minig/ heavy industory</h2>
                <Text_box data={mining_data} />
              </div>
              <div className="border"></div>
          </div>
        </section>
      </div>
      </Layout>
    </>
  )
}