import React, { useEffect, useState } from 'react';
import './modal.css';
import { Link } from "gatsby";



let image_index = 0;
const Modal = (props) => {

  const [pick_image, setPick_image] = useState("");
  const [category_name, setCategory_name] = useState("");
  // const [max_number, setMax_number] = useState();

  const data_sources = props.data_sources;
  let max_number = props.max_number;

  // console.log("modal max " + max_number);

  const handleItem = (image_index) => {
    setPick_image(data_sources[image_index].image);
    setCategory_name(data_sources[image_index].category);
  }

  useEffect(() => {
    image_index = props.clicked_index;
    handleItem(image_index);
  }, [])

  const handleArrow = (arrowNum) => {
    
    if(arrowNum === 1) {
      if(image_index === max_number) {
        image_index = 0;
        handleItem(image_index);
      }
      else {
        image_index++;
        handleItem(image_index);
      }
    }
    else if(arrowNum === 0){
      if(image_index === 0) {
        image_index = max_number;
        handleItem(image_index);
      }
      else {
        image_index--;
        handleItem(image_index);
      }
    }
  }


  return (
    <div className="modal_container">
      <div className="white_box">
        <div className="left_box">
          <button 
            className="left_modal_button"
            onClick={() => handleArrow(0)}
          >
          <i className="fas fa-chevron-left fontFour"></i>
          </button>
        </div>
        <div className="close_box">
          <button 
            className="close_modal_button"
            onClick={props.close_modal}
          >&times;</button>
        </div>
        <div className="link_box">
          <button className="link_category_button">
            <Link to={"/projects/#" + category_name}>{category_name}</Link>
          </button>
        </div>
        <div className="title_box">
          <h2 className="modal_title fontTwo">Project Title</h2>
        </div>
        <div className="image_box">
          <img 
            src={pick_image}
            className= "modal_image"
            alt="images in modal"
          ></img>
        </div>
        <div className="comment_box">
          <h3 className="modal_comment fontTwo">
            I am the Goran san ! Bla bla bla bla bla~
          </h3>
        </div>
        <div className="right_box">
          <button 
            className="right_modal_button"
            onClick={() => handleArrow(1)}
          >
            <i className="fas fa-chevron-right fontFour"></i>
          </button>
        </div>
      </div>
    </div>
  )
}

export default Modal;